// mixin
import ajax from 'element-ui/packages/upload/src/ajax'

export default {
  data () {
    return {
      // :  upload 
      // Vue.use(EleForm, { upload: { action: '' } })
      isUploadComponent: true
    }
  },
  methods: {
    // 
    handleImageUpload (file, callback) {
      const attrs = this.attrs
      if (!file) {
        this.$message.error(', !')
        return
      }
      if (attrs.fileSize) {
        const isLt = file.size / 1024 / 1024 < attrs.fileSize

        if (!isLt) {
          this.$message.error(` ${attrs.fileSize} MB!`)
          return
        }
      }
      const isImg = file.type.includes('image')
      if (!isImg) {
        this.$message.error(', !')
        return
      }
      if (!attrs.action) {
        this.$message.error(' action !')
        return
      }

      ajax({
        action: attrs.action,
        file: file,
        filename: attrs.name,
        data: attrs.data,
        onProgress () {
          return false
        },
        onSuccess: response => {
          if (attrs.responseFn) {
            file.url = URL.createObjectURL(file)
            response = attrs.responseFn(response, file)
          }
          callback(response)
        },
        onError: error => {
          this.$message.error(', ')
          console.error(error)
        }
      })
    }
  }
}
