<template>
  <el-tabs
    v-bind="attrs"
    v-model="currentName"
    v-on="tabOn"
  >
    <el-tab-pane
      :key="key"
      :label="item.label"
      :name="key"
      v-for="(item, key) of formDesc"
    >
      <ele-form
        :form-data="formData[key]"
        :form-desc="item.desc"
        :formBtns="formBtns[key]"
        :rules="rules[key]"
        v-bind="$attrs"
        v-if="key === currentName"
        v-on="$listeners"
      />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
  name: 'EleFormGroup',
  props: {
    // tab https://element.eleme.cn/#/zh-CN/component/tabs#tabs-attributes
    tabAttrs: Object,
    // tab, https://element.eleme.cn/#/zh-CN/component/tabs#tabs-events
    tabOn: Object,
    // tab
    activeName: String,
    /**
      * formDesc: {
      *   groupKey1: {
      *     label: '1',
      *     desc: {
      *       // 1
      *       name: {
      *         type: 'input',
      *         label: ''
      *       },
      *       // 2
      *       job: {
      *         type: 'input',
      *         label: ''
      *       }
      *     }
      *   },
      *   groupKey2: {
      *     label: '2',
      *     desc: {
      *       // 1
      *       oldPassword: {
      *         type: 'password',
      *         label: ''
      *       },
      *       // 2
      *       newPassword: {
      *         type: 'password',
      *         label: ''
      *       },
      *       // 3
      *       confirmPassword: {
      *         type: 'password',
      *         label: ''
      *       }
      *     }
      *   }
     */
    formDesc: {
      type: Object,
      required: true
    },
    /**
     * formData: {
     *    groupKey1: {
     *       name: 'xx',
     *       job: ''
     *    },
     *    groupKey2: {
     *       oldPassword: '123',
     *       newPassword: null,
     *       confirmPassword: null
     *    }
     * }
     */
    formData: {
      type: Object,
      required: true
    },
    /**
     * rules: {
     *    groupKey1: {
     *       // ...
     *    },
     *    groupKey2: {
     *       // ...
     *    }
     * }
     */
    rules: {
      type: Object,
      default () { return {} }
    },
    /**
     * formBtns: {
     *    groupKey1: {
     *       // ...
     *    },
     *    groupKey2: {
     *       // ...
     *    }
     * }
     */
    formBtns: {
      type: Object,
      default () { return {} }
    }
  },
  computed: {
    attrs () {
      return Object.assign({}, { type: 'border-card' }, this.tabAttrs)
    }
  },
  data () {
    return {
      currentName: ''
    }
  },
  created () {
    this.currentName = this.activeName || Object.keys(this.formDesc)[0]
  }
}
</script>
